import React, { useEffect, useState } from 'react';
import io from 'socket.io-client';
import {Alert, Container, LinearProgress, ThemeProvider, Typography} from "@mui/material";
import theme from "./MainTheme";

// Connect to your WebSocket server
const socket = io('https://telemetria-server.onrender.com');

function GPSAccelerometer() {
  const [message, setMessage] = useState({});
  const [lastPosition, setLastPosition] = useState(null);
  const [lastTimestamp, setLastTimestamp] = useState(null);

  // Function to calculate distance between two lat/lng points (Haversine formula)
  const calculateDistance = (lat1, lng1, lat2, lng2) => {
    const R = 6371; // Radius of the Earth in km
    const dLat = ((lat2 - lat1) * Math.PI) / 180;
    const dLng = ((lng2 - lng1) * Math.PI) / 180;
    const a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos((lat1 * Math.PI) / 180) *
        Math.cos((lat2 * Math.PI) / 180) *
        Math.sin(dLng / 2) *
        Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c; // Distance in km
  };

  // Function to calculate speed (in km/h or m/s)
  const calculateSpeed = (distance, timeDifference, unit = 'km/h') => {
    const speed = distance / (timeDifference / 1000); // speed in km/h
    return unit === 'km/h' ? speed : speed / 3.6; // convert to m/s if needed
  };

  useEffect(() => {
    // Function to send GPS data along with speed
    const sendGPSData = (position) => {
      const { latitude, longitude, speed } = position.coords;
      const gpsData = {
        lat: latitude,
        lng: longitude,
        speed: speed || 0, // Use speed from GPS if available, otherwise fallback to 0
      };

      setMessage({message:'Enviando seal GPS', severity:'success'});
      socket.emit('gpsData', gpsData); // Send GPS data to WebSocket server
    };

    // Function to handle GPS position success
    const handleGPSSuccess = (position) => {
      const { latitude, longitude, timestamp } = position.coords;

      if (lastPosition && lastTimestamp) {
        const distance = calculateDistance(
            lastPosition.latitude,
            lastPosition.longitude,
            latitude,
            longitude
        );
        const timeDifference = timestamp - lastTimestamp;

        // Calculate speed in km/h (or m/s if you prefer)
        const speed = calculateSpeed(distance, timeDifference, 'km/h');
        console.log(`Speed: ${speed} km/h`);

        // Send GPS data along with calculated speed
        sendGPSData({ ...position.coords, speed });
      }

      // Update last position and timestamp
      setLastPosition({ latitude, longitude });
      setLastTimestamp(timestamp);
    };

    // Function to handle GPS position error
    const handleGPSError = (error) => {
      console.error('Error getting GPS position:', error);
      setMessage({message: 'Error con el GPS', severity:'error'});
    };

    // Requesting GPS permission and getting current position
    if (navigator.geolocation) {
      navigator.geolocation.watchPosition(handleGPSSuccess, handleGPSError, {
        enableHighAccuracy: true,
        maximumAge: 0,
        timeout: 5000,
      });
    } else {
      console.error('Geolocation is not supported by this browser.');
    }

    // Function to send accelerometer data
    const handleMotion = (event) => {
      const { acceleration } = event;
      if (acceleration) {
        const accelerometerData = {
          x: acceleration.x,
          y: acceleration.y,
          z: acceleration.z,
        };

        socket.emit('accelerometerData', accelerometerData); // Send accelerometer data to WebSocket server
      }
    };

    // Adding event listener for device motion
    window.addEventListener('devicemotion', handleMotion);

    // Cleanup function to remove event listeners
    return () => {
      window.removeEventListener('devicemotion', handleMotion);
    };
  }, [lastPosition, lastTimestamp]);

  return (
      <ThemeProvider theme={theme}>
        <Container>
          <Typography variant="h1">GPS and Accelerometer Data Sender</Typography>
          <LinearProgress/>
          {message ? <Alert severity={message.severity}>{message.message}</Alert> : <></>}

        </Container>
      </ThemeProvider>
  );
}

export default GPSAccelerometer;
